import * as md from 'vscode-markdown-languageservice';
import { ConfigurationManager } from './configuration';
import { Disposable } from './util/dispose';

export class LogFunctionLogger extends Disposable implements md.ILogger {

	private static f(): string {
		const now = new Date();
		return String(now.getUTCHours()).padStart(2, '0')
			+ ':' + String(now.getMinutes()).padStart(2, '0')
			+ ':' + String(now.getUTCSeconds()).padStart(2, '0') + '.' + String(now.getMilliseconds()).padStart(3, '0');
	}

	private static g(data: any): string {
		if (data instanceof Error) {
			if (typeof data.stack === 'string') {
				return data.stack;
			}
			return data.message;
		}
		if (typeof data === 'string') {
			return data;
		}
		return JSON.stringify(data, undefined, 2);
	}

	private h: md.LogLevel;

	constructor(
		private readonly i: typeof console.log,
		private readonly j: ConfigurationManager,
	) {
		super();

		this.c(this.j.onDidChangeConfiguration(() => {
			this.h = LogFunctionLogger.k(this.j);
		}));

		this.h = LogFunctionLogger.k(this.j);
	}

	private static k(config: ConfigurationManager): md.LogLevel {
		switch (config.getSettings()?.markdown.server.log) {
			case 'trace': return md.LogLevel.Trace;
			case 'debug': return md.LogLevel.Debug;
			case 'off':
			default:
				return md.LogLevel.Off;
		}
	}

	get level(): md.LogLevel { return this.h; }

	public log(level: md.LogLevel, message: string, data?: any): void {
		if (this.level < level) {
			return;
		}

		this.m(`[${this.l(level)} ${LogFunctionLogger.f()}] ${message}`);
		if (data) {
			this.m(LogFunctionLogger.g(data));
		}
	}

	private l(level: md.LogLevel): string {
		switch (level) {
			case md.LogLevel.Off: return 'Off';
			case md.LogLevel.Debug: return 'Debug';
			case md.LogLevel.Trace: return 'Trace';
		}
	}

	private m(value: string): void {
		this.i(value);
	}
}
